.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 10rem;
  /* background-color: #ffffff; */
  background-color: #1B1B25;
  color: #fff;
  padding: 1rem;
  width: 400px;
  border-radius: 8px;

}

.Modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  /* background-color: #F5F5F5; */
  background-color: #1B1B25;
  color: #fff;
  padding: 0.5rem 1rem;
  font-weight: bold;
  font-size: 2rem;
  border-radius: 8px;

}